@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
footer {
  position: fixed;
  bottom: 0;
  margin-top: 2em;
  text-align: center;
  color: var(--color-light);
  background: var(--color-primary);
  padding: 0.25em 0.3em;
  width: 100%;
}

header {
  background: var(--color-gray-100);
  text-align: center;
  padding: 2em;
}

header img {
  max-width: 96px;
  margin-right: 0.25em;
}

.btn {
  display: inline-block;
  font-weight: 400;
  color: var(--color-dark);
  text-align: center;
  vertical-align: middle;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  -webkit-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn-primary {
  color: var(--color-light);
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}

.btn-primary:hover {
  color: var(--color-light);
  background-color: var(--color-hover-primary-bg);
  border-color: var(--color-hover-primary-border);
}

.form-control {
  display: block;
  width: 100%;
  max-width: 300px;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--color-gray-500);
  background-color: var(--color-light);
  background-clip: padding-box;
  border: 1px solid var(--color-gray-200);
  border-radius: 0.25rem;
  -webkit-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
  color: var(--color-gray-500);
  background-color: var(--color-light);
  border-color: var(--color-primary);
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(247, 110, 110, 0.25);
}

.new-member-form {
  margin: 2em auto 4em;
  text-align: center;
  max-width: 450px;
}

.new-member-title:after {
  content: '';
  display: block;
  margin: 1.5em auto;
  background-color: var(--color-dark);
  width: 100%;
  max-width: 125px;
  height: 2.5px;
}

@media screen and (max-width: 319px) {
  .form-group > input {
    margin-bottom: 1em;
  }
}

@media screen and (min-width: 320px) {
  .form-group {
    display: flex;
    justify-content: center;
  }
}

@media screen and (min-width: 576px) {
  .new-member-form {
    grid-template-columns: 60% 40%;
    grid-template-areas:
      'label label'
      'input button';
  }
}

.alert {
  position: relative;
  padding: 0.75em;
  margin-bottom: 1em;
  border: 1px solid transparent;
  border-radius: 0.25em;
}

.alert-danger {
  color: var(--color-alert-danger);
  background-color: var(--color-alert-danger-bg);
  border-color: var(--color-alert-danger-border);
}

.alert-success {
  color: var(--color-alert-success);
  background-color: var(--color-alert-success-bg);
  border-color: var(--color-alert-success-border);
}

.member-item {
  padding: 1em;
  background-color: var(--color-gray-100);
  width: 100%;
  border-radius: 0.25rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
  text-align: center;
}

.member-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1.25em;
}

@media (min-width: 576px) {
  .member-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .member-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.member-list-title:after {
  content: '';
  display: block;
  margin: 1.5em auto;
  background-color: var(--color-dark);
  width: 100%;
  max-width: 125px;
  height: 2.5px;
}

:root {
  --color-light: #fff;
  --color-dark: #151718;
  --color-gray-100: #f4f4f4;
  --color-gray-200: #dacece;
  --color-gray-500: #495057;
  --color-primary: #f76c6c;
  --color-hover-primary-bg: #cc4242;
  --color-hover-primary-border: #b64747;

  --color-alert-danger: #842029;
  --color-alert-danger-bg: #f8d7da;
  --color-alert-danger-border: #f5c2c7;

  --color-alert-success: #0f5132;
  --color-alert-success-bg: #d1e7dd;
  --color-alert-success-border: #badbcc;
}

*,
::after,
::before {
  box-sizing: border-box;
}

button,
a {
  cursor: pointer;
}

.mr-1 {
  margin-right: 1em;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.App {
  min-height: 100vh;
}

main {
  max-width: 960px;
  margin: 0 auto;
  margin-bottom: 100px;
  padding: 0 1em;
}

@media (min-width: 414px) {
  main {
    margin-bottom: 80px;
  }
}

